#section-product {
    .text-note {
        text-align: center;
        margin-top: 15px;
        font-size: 16px;
    }
}

.product {
    margin: 15px;
    margin-bottom: 30px;
    width: 280px;
    height: 350px;
    position: relative;
    border: 1px solid rgba($color: #222, $alpha: 0.2);
    &-block {
        padding: 15px 0;
    }
    &-list {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }
    &__img {
        width: 100%;
        height: 100%;
        background-size: cover;
    }
    &__info {
        position: absolute;
        bottom: -20px;
        color: #222;
        background-color: rgba(#fff, 0.8);
        width: calc(100% - 20px);
        left: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 10px 0;
        border: 1px solid #222;
    }
    &__title {
        font-size: 24px;
        border-bottom: 1px solid #222;
        padding-bottom: 4px;
        pointer-events: none;
    }
    &__price {
        font-size: 20px;
        margin-top: 10px;
        pointer-events: none;
    }
}