@media screen and(min-width:768px) {
    .main-nav {
        border-bottom: 2px solid;
        border-bottom-color: transparent;
        transition: 0.3s all ease-in-out;
        .nav-list {
            &__logo {
                img {
                    transition: .3s all ease-in-out;
                }
            }
        }
        &.active {
            padding: 5px 0;
            background-color: rgba($primaryColor, 0.6);
            width: 100%;
            top: 0;
            border-bottom-color: rgba(#222, 0.2);
            .nav-list {
                &__logo {
                    img {
                        width: 80px;
                    }
                }
            }
        }
    }
}

@media screen and(max-width:1180px) {
    .container {
        padding: 0 15px;
    }
    #section-info {
        .advantages-list {
            flex-wrap: wrap;
            &__item {
                flex-basis: 47%;
                margin-bottom: 30px;
            }
        }
        .promo {
            width: 80%;
        }
    }
}

@media screen and(max-width:768px) {
    .row.two-col {
        .column {
            flex-basis: 100%;
        }
    }
    #section-advantages {
        .row {
            flex-wrap: wrap;
            flex-direction: column-reverse;
        }
        .column+.column {
            margin-left: 0;
            margin-bottom: 60px;
        }
        .advantage {
            justify-content: center;
            text-align: center;
            &__desc {
                max-width: 80%;
            }
        }
    }
    #section-contact {
        .row {
            flex-wrap: wrap;
            flex-direction: column-reverse;
        }
        .column+.column {
            margin-bottom: 30px;
        }
        .column {
            justify-content: flex-start;
            padding: 0 30px;
        }
        #form-contact {
            width: 100%;
        }
    }
    .main-nav {
        border-bottom: 2px solid rgba(#222, 0.2);
        background-color: #EBB241;
    }
    .nav-list {
        transform: translateX(-100%);
        position: absolute;
        transition: 0.5s all ease-in-out;
        top: 72px;
        flex-direction: column;
        background-color: #EBB241;
        width: 100%;
        opacity: 0;
        padding: 30px 0;
        border-bottom: 2px solid rgba(#222, 0.2);
        &.active {
            opacity: 1;
            transform: translateX(0);
        }
        &__item {
            margin: 15px 0;
            a {
                font-size: 24px;
            }
        }
        &__logo {
            img {
                width: 100px;
            }
        }
    }
    .nav-toggle {
        display: flex;
    }
}

@media screen and(max-width:600px) {
    #section-info {
        .advantages-list {
            flex-wrap: wrap;
            &__item {
                flex-basis: 95%;
                margin-bottom: 30px;
            }
        }
        .promo {
            width: 100%;
        }
    }
    #section-contact {
        .column {
            padding: 0 15px;
        }
        .column:nth-child(1) {
            padding: 0 15px;
        }
    }
}

@media screen and(max-width:425px) {
    .container {
        padding: 0 5px;
    }
    #section-advantages {
        .advantage {
            text-align: left;
            &__desc {
                max-width: 100%;
                padding: 0 30px;
            }
        }
    }
    .section__img {
        img {
            width: 100%;
        }
    }
    h1.title {
        font-size: 48px;
    }
    .mt-30 {
        margin-top: 15px;
    }
}

@media screen and(max-width:375px) {
    #section-contact {
        .column {
            padding: 0 5px;
        }
        .column:nth-child(1) {
            padding: 0 5px;
        }
    }
}