body {
    font-family: 'Open Sans', sans-serif;
}

.container {
    max-width: 1180px;
    width: 100%;
    margin: 0 auto;
}

.section {
    padding: 30px 0;
    #section-advantages,
    #section-welcome {
        padding: 60px 0;
    }
}

.row {
    display: flex;
    &.two-col {
        .column {
            flex-basis: 50%;
        }
    }
}

.text {
    &-primary {
        font-family: 'Montserrat', sans-serif;
    }
    &-uppercase {
        text-transform: uppercase;
    }
    &-shadow {
        text-shadow: 1px 1px 2px black;
    }
    &-note {
        color: #979797;
        font-style: italic;
    }
}

h1.title {
    font-size: 72px;
}

h3.title {
    font-size: 24px;
}

h2.title {
    text-align: center;
    font-size: 36px;
    font-weight: 700;
}

.bold {
    font-weight: 700;
}

.promo {
    display: flex;
    align-items: center;
    padding: 10px 15px;
    background: #fff;
    justify-content: center;
    width: 50%;
    align-self: center;
    margin: 0 auto;
    margin-top: 30px;
    p {
        color: #222;
        font-size: 18px;
        margin-right: 15px;
        font-weight: 500;
    }
    svg {
        height: 50px;
        width: 50px;
    }
}

.nav-toggle {
    height: 40px;
    width: 60px;
    display: none;
    justify-content: space-between;
    align-items: center;
    flex-direction: column-reverse;
    .line {
        display: block;
        height: 4px;
        width: 100%;
        background-color: #fff;
        transition: .5s all ease-in-out;
    }
    &.active {
        justify-content: center;
        .line:nth-child(2) {
            transform: rotate(45deg);
            margin-top: -3px;
        }
        .line:nth-child(1) {
            opacity: 0;
            transform: rotate(60deg);
        }
        .line:nth-child(3) {
            transform: rotate(-45deg);
        }
    }
}