#section-advantages {
    background-color: $primaryColor;
    padding: 60px 0;
    .column {
        display: flex;
        justify-content: center;
        flex-direction: column;
        &:nth-child(1) {
            align-items: center;
        }
    }
    .column+.column {
        margin-left: 60px;
    }
    .section__img+.section__img {
        margin-top: 30px;
    }
    .advantages-list {
        .advantages-list__item+.advantages-list__item {
            margin-top: 30px;
        }
    }
}

.advantage {
    color: #fff;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    &__title {
        font-size: 24px;
        font-weight: 500;
        margin-left: 15px;
    }
    &__img {
        height: 70px;
        width: 70px;
        svg {
            fill: #fff;
            height: 70px;
            width: 70px;
        }
    }
    &__desc {
        width: 100%;
        margin-top: 15px;
        p {
            font-size: 18px;
            line-height: 24px;
        }
    }
}