#section-contact {
    background-color: $primaryColor;
    color: #fff;
    .column {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        &:nth-child(1) {
            padding: 0 30px;
        }
    }
    iframe {
        width: 100%;
    }
    .row {
        margin-top: 30px;
    }
}

.contact-list {
    width: 100%;
    padding: 15px 0;
    margin-top: 15px;
    &__item+&__item {
        margin-top: 25px;
    }
    &__item {
        svg {
            height: 50px;
            width: 50px;
        }
        p,
        a {
            display: flex;
            align-items: center;
            color: #fff;
            font-size: 16px;
            line-height: 20px;
            span {
                display: block;
                margin-left: 15px;
            }
        }
    }
}

#form-contact {
    background-color: #fff;
    padding: 30px;
    width: 80%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    border: 1px solid rgba($color: #222, $alpha: .2);
    .form-title {
        margin-bottom: 30px;
    }
    h3 {
        text-align: center;
        font-size: 24px;
        margin: 5px 0;
        color: #222;
    }
    .input-wrapper {
        width: 100%;
        input,
        textarea {
            height: 40px;
            width: 100%;
            border: 1px solid rgba($color: #222, $alpha: .2);
            resize: none;
            margin: 10px 0;
            padding: 10px 15px;
            font-size: 16px;
            font-family: 'Montserrat', sans-serif;
        }
        textarea {
            height: 120px;
        }
    }
    .submit {
        padding: 10px 30px;
        background-color: #EBB241;
        font-size: 18px;
        font-family: 'Montserrat', sans-serif;
        margin-top: 15px;
        color: #fff;
        font-weight: 600;
    }
}