#section-info {
    background-color: $primaryColor;
    color: #fff;
    .advantages-list {
        display: flex;
        flex-direction: row;
        margin-top: 30px;
        &__item {
            flex-basis: 25%;
            width: unset;
            border: 1px solid rgba(#fff, 0.3);
            padding: 10px;
            margin: 0 10px;
        }
        .advantage {
            flex-direction: column;
            align-items: center;
            justify-content: center;
            text-align: center;
            padding-top:15px;
            &__title {
                height: 50px;
                display: flex;
                align-items: center;
                justify-content: center;
                height: 70px;
                margin-top: 15px;
                width: 100%;
                margin-left: 0;
                font-weight: 700;
                border-bottom: 1px solid rgba(#fff, 0.3);
            }
            &__desc {
                margin-top: 0;
                padding-bottom: 5px;
                p {
                    margin-bottom: 10px;
                    &:first-child{
                        border-bottom: 1px solid rgba(#fff, 0.3);
                    }
                }
                a {
                    color: #fff;
                    font-size: 16px
                }
            }
        }
    }
}