#section-welcome {
    height: 100vh;
    background: url(../../img/section1.webp) center center no-repeat;
    background-size: cover;
    color: #fff;
    padding: 60px 0;
    min-height: 600px;
    .container {
        height: 100%;
        display: flex;
        align-items: flex-end;
        justify-content: center;
    }
    h1.title {
        font-family: 'Open Sans', sans-serif;
    }
    h3.title {
        text-align: center;
    }
}