.main-nav {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding: 15px 0;
    z-index: 999;
}

.nav-list {
    display: flex;
    align-items: center;
    justify-self: center;
    &__item {
        margin: 0 15px;
    }
    &__logo {
        margin: 0 30px;
        img {
            width: 150px;
        }
    }
    &__item {
        a {
            font-weight: 600;
            font-size: 18px;
            color: #fff;
        }
    }
}